import { create } from 'zustand';
import { persist, createJSONStorage } from 'zustand/middleware';

export type Language = 'fr' | 'ar' | 'en';

export interface CartItem {
    id: string;
    productId: string;
    name: string;
    quality: string;
    type: string;
    quantityKg: number;
    pricePerKg: number;
    image: string;
}

export interface InventoryItem {
    id: string;
    type: string;
    quality: string;
    pricePerKg: number;
    purchasePrice?: number; // For Profit Calculation
    stockKg: number;
    images: string[];
    description: string;
    origin?: string;
    longDescription?: string;

    // Arabic Translations
    typeAr?: string;
    qualityAr?: string;
    originAr?: string;
    descriptionAr?: string;
    longDescriptionAr?: string;

    // English
    nameEn?: string;
    typeEn?: string;
    qualityEn?: string;
    originEn?: string;
    descriptionEn?: string;
    longDescriptionEn?: string;

    nutritionalInfo?: {
        calories: number;
        carbs: number;
        protein: number;
        fiber: number;
    };
}

export interface Order {
    id: string;
    userId?: string;
    customer: {
        name: string;
        phone: string;
        wilaya: string;
        commune: string;
        address?: string;
    };
    items: CartItem[];
    total: number;
    status: 'pending' | 'completed' | 'cancelled' | 'returned';
    date: string;
    returnReason?: string;
    source?: string; // 'instagram', 'facebook', 'tiktok', 'direct', etc.
}

export interface SiteSettings {
    heroImage: string;
    heroImageDesktop?: string;
    deliveryFees: Record<string, number>; // Wilaya name -> Price
}

export interface User {
    id: string;
    email: string;
    password?: string;
    role: 'admin' | 'customer';
    name: string;
    phone?: string;
    wilaya?: string;
    commune?: string;
}

