const DEFAULT_DELIVERY_FEES: Record<string, number> = WILAYAS.reduce((acc, w) => {
    const northWilayas = ['Alger', 'Blida', 'Boumerdès', 'Tipaza', 'Oran', 'Constantine', 'Annaba', 'Sétif'];
    const southWilayas = ['Adrar', 'Tamanrasset', 'Illizi', 'Tindouf', 'Béchar', 'Ouargla', 'Ghardaïa'];

    if (northWilayas.includes(w)) acc[w] = 400;
    else if (southWilayas.includes(w)) acc[w] = 900;
    else acc[w] = 600;
    return acc;
}, {} as Record<string, number>);

